import React from 'react';

import {BrowserRouter as Router,Route} from 'react-router-dom'

import About from './screens/about/about'
import AudioBook from './screens/audiobook/audiobooks'

import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Articles from './screens/articles/articles';
import DetailsPage from './screens/detailpage/detailpage';
import NavBar from './components/navbar';


function App() {
  return (
    <Router>
      <NavBar/>
      <Route path='/' component={Articles} exact/>
      <Route path='/details/:title/:details/:images' component={DetailsPage} />
      <Route path='/about' component={About} />
      <Route path='/adibk' component={AudioBook} />
    </Router>
  );
}

export default App;